export const getPositionById = (
  positions: PositionAPI[],
  position_id: string
): PositionAPI | undefined => {
  console.log({
    positions,
    position_id,
    data: positions.filter(
      (position) => position.position_id === position_id
    )[0],
  });
  return positions.find((position) => position.position_id === position_id);
};

export const getSchoolById = (
  schools: SchoolInfoInterface[],
  school_id: string
): SchoolInfoInterface => {
  return schools.filter((school) => school.school_id === school_id)[0];
};

export const getPostById = (
  posts: PostSchoolInterface[],
  post_id: string
): PostSchoolInterface => {
  return posts.filter((post) => post.school_id === post_id)[0];
};

export const getPostsBySchoolId = (
  posts: PostSchoolInterface[],
  school_id: string
): PostSchoolInterface[] => {
  return posts.filter((post) => post.school_id === school_id);
};

export const getPostsByIds = (
  posts: PostSchoolInterface[],
  school_id: string,
  position_id: string
): PostSchoolInterface[] => {
  return posts.filter(
    (post) => post.school_id === school_id && post.position_id === position_id
  );
};

export const displayDate = (dateToDisplay: Date): string => {
  return new Intl.DateTimeFormat("en-GB", {
    year: "numeric",
    month: "long",
    day: "2-digit",
  }).format(dateToDisplay);
};

