import axios from "axios";
import React, { Component } from "react";
import { GiTeacher } from "react-icons/gi";
import { connect } from "react-redux";
import { Redirect } from "react-router-dom";
import { Auth, PushPopupAlert } from "../../actions";
import {
  fetchDistrictPosts,
  getDistrictPlacement,
  updateSchoolPost,
} from "../../actions/schools";
import { getPositions } from "../../actions/settings";
import { PopupAlertData } from "../../components/Alert/Popup";
import Modal, { Themes, ModalSize } from "../../components/Modal/Modal";
import Table from "../../components/Table/Table";
import { PositionAPI } from "../../interfaces";
import {
  placedCandidateInterface,
  PlacedCandidateStatus,
} from "../../interfaces/marks_publication";
import { PopupAlertTheme } from "../../interfaces/popupAlert";
import { StoreState } from "../../reducers";
import {
  capitalizeFirstLetter,
  getPositionById,
  isEmptyOrSpaces,
} from "../../shared/dataTest";
import {
  PostSchoolInterface,
  PostRequestStatusInterface,
} from "../../shared/interfaces";
import { API } from "../../utils/api";
import { errorToText } from "../../utils/errors";
import { DATE } from "../../utils/functions";
import { showRequestStatus } from "../HeadTeacher/ReceivePost";
import { getPlacementByRequest } from "../HeadTeacher/RequestTeacher";
import ViewPosition from "../Positions/ViewPosition";

interface AppProps {
  auth: Auth;
  systemPositions: PositionAPI[] | null;
  districtPosts: PostSchoolInterface[];
  districtPlacements: placedCandidateInterface[] | null;
  getPositions: Function;
  fetchDistrictPosts: Function;
  updateSchoolPost: Function;
  getSchools: Function;
  getDistrictPlacement: Function;
  PushPopupAlert: (alert: PopupAlertData) => void;
}

interface AppState {
  modalState: "CONFIRM" | "UPDATE" | null;
  filterByPosition: string;
  loadingRequest: boolean;
  loading: boolean;
  loadingPost: boolean;
  thisPost: PostSchoolInterface | null;
  viewPost: boolean;
  approvedPosts: number;
  formError: {
    element: string;
    msg: string;
  } | null;
}

export class _District extends Component<AppProps, AppState> {
  constructor(props: AppProps) {
    super(props);

    this.state = {
      modalState: null,
      filterByPosition: "",
      loadingRequest: false,
      loading: true,
      loadingPost: true,
      thisPost: null,
      viewPost: false,
      approvedPosts: 0,
      formError: null,
    };
  }

  setModalState = (value: "CONFIRM" | "UPDATE" | null) => {
    this.setState({ modalState: value });
  };

  setFilterByPosition = (value: string) => {
    this.setState({ filterByPosition: value });
  };

  setLoadingRequest = (value: boolean) => {
    this.setState({ loadingRequest: value });
  };
  setLoading = (value: boolean) => {
    this.setState({ loading: value });
  };
  setLoadingPost = (value: boolean) => {
    this.setState({ loadingPost: value });
  };

  setThisPost = (value: PostSchoolInterface | null) => {
    this.setState({ thisPost: value });
  };

  setViewPost = (value: boolean) => {
    this.setState({ viewPost: value });
  };

  setApprovedPosts = (value: number) => {
    this.setState({ approvedPosts: value });
  };

  setFormError = (
    value: {
      element: string;
      msg: string;
    } | null
  ) => {
    this.setState({ formError: value });
  };

  componentDidMount = () => {
    if (!isEmptyOrSpaces(this.props.auth.user_access.district_id)) {
      if (this.props.systemPositions === null) {
        this.setLoading(true);
        this.props.getPositions((status: boolean, msg: string) => {
          this.setLoading(status);
          console.log({ status: status, msg: msg });
          if (msg.length) {
            this.props.PushPopupAlert({
              title: msg,
              theme: PopupAlertTheme.danger,
            });
          }
        });
      }
      if (!this.props.districtPosts.length && this.state.loadingPost) {
        this.props.fetchDistrictPosts(
          this.props.auth.user_access.district_id,
          (status: boolean, message: string) => {
            this.setLoadingPost(status);
            if (!status) {
              if (message.length > 0 && this.state.loadingPost) {
                console.error("error loading school post: ", message);
                this.props.PushPopupAlert({
                  title: message,
                  theme: PopupAlertTheme.danger,
                });
              }
            }
          }
        );
      } else if (this.state.loadingPost) {
        this.setLoadingPost(false);
      }

      if (!this.props.districtPlacements) {
        this.props.getDistrictPlacement(
          this.props.auth.user_access.district_id,
          (status: boolean, message: string) => {
            this.setLoading(status);
            if (!status) {
              if (message.length > 0 && status) {
                console.error("error loading school post: ", message);
                this.props.PushPopupAlert({
                  title: message,
                  theme: PopupAlertTheme.danger,
                });
              }
            }
          }
        );
      } else if (this.state.loading) {
        this.setLoading(false);
      }
    }
  };

  tableData = (): JSX.Element[] => {
    if (this.state.loadingPost) {
      return [
        <tr key={1} className="border-b border-gray-200 hover:bg-gray-100">
          <td className="p-3 text-center" colSpan={8}>
            Loading post request(s)
          </td>
        </tr>,
      ];
    }

    let data = this.props.districtPosts;

    data =
      this.state.filterByPosition !== "" &&
      this.state.filterByPosition !== "all"
        ? data.filter(
            (schoolPost) =>
              schoolPost.position_id === this.state.filterByPosition
          )
        : data;

    if (data.length === 0) {
      return [
        <tr key={1} className="border-b border-gray-200 hover:bg-gray-100">
          <td className="p-3 text-center" colSpan={8}>
            No Data Found
          </td>
        </tr>,
      ];
    }

    return data.map((schoolPost, index) => (
      <tr
        key={index + 1}
        className="border-b border-gray-200 hover:bg-gray-100"
      >
        <td className="py-3 px-4 text-left">{schoolPost.school.school_name}</td>
        <td className="py-3 px-4 text-left">
          {this.props.systemPositions &&
          getPositionById(this.props.systemPositions, schoolPost.position_id)
            ? getPositionById(
                this.props.systemPositions,
                schoolPost.position_id
              )!.position_title
            : "Unknown position"}
        </td>
        <td className="py-3 px-4 text-left text-base font-bold">
          {schoolPost.needed_posts}
        </td>
        <td className="py-3 px-4 text-left text-base font-medium">
          {schoolPost.requested_post_status !==
          PostRequestStatusInterface.INITIATED
            ? schoolPost.confirmed_posts
            : "-"}
        </td>
        <td className="py-3 px-4 text-left text-base font-medium">
          {schoolPost.occupied_posts
            ? schoolPost.occupied_posts +
              (schoolPost.requested_post_status ===
              PostRequestStatusInterface.RECEIVED
                ? schoolPost.received_posts
                  ? schoolPost.received_posts
                  : 0
                : 0)
            : "-"}
        </td>
        <td className="py-3 px-4 text-left text-base font-medium">
          {schoolPost.requested_post_status !==
          PostRequestStatusInterface.INITIATED
            ? (schoolPost.confirmed_posts ? schoolPost.confirmed_posts : 0) -
              (schoolPost.requested_post_status ===
              PostRequestStatusInterface.RECEIVED
                ? schoolPost.received_posts
                  ? schoolPost.received_posts
                  : 0
                : 0)
            : "-"}
        </td>
        <td>
          <span
            className={`px-2 py-1 font-semibold rounded ${
              schoolPost.requested_post_status ===
              PostRequestStatusInterface.INITIATED
                ? "text-gray-700 bg-gray-100 dark:bg-gray-700 dark:text-gray-100"
                : schoolPost.requested_post_status ===
                  PostRequestStatusInterface.CONFIRMED
                ? "text-blue-700 bg-blue-100 dark:bg-blue-700 dark:text-blue-100"
                : "text-green-800 bg-green-100 dark:bg-green-800 dark:text-green-100"
            }`}
          >
            {schoolPost.requested_post_status ===
            PostRequestStatusInterface.INITIATED
              ? "Pending"
              : capitalizeFirstLetter(schoolPost.requested_post_status)}
          </span>
        </td>
        <td className="py- text-left">
          <div className="flex item-center justify-end gap-1">
            {schoolPost.requested_post_status ===
              PostRequestStatusInterface.INITIATED && (
              <button
                className="
                btn
                btn-blue
                transition
                ease-out
                duration-500
                flex items-center
              "
                title="View User Profile"
                onClick={() => {
                  this.setThisPost(schoolPost);
                  this.setModalState("CONFIRM");
                }}
              >
                Reply
              </button>
            )}
            <button
              className="
                btn
                bg-gray-400
                text-gray-50
                hover:bg-gray-600
                transition
                ease-out
                duration-500
                flex items-center
              "
              title="Request details"
              onClick={() => {
                this.setThisPost(schoolPost);
                this.setViewPost(true);
              }}
            >
              Details
            </button>
          </div>
        </td>
      </tr>
    ));
  };

  doPlacement = async (thisPost: PostSchoolInterface) => {
    try {
      const res = await axios.post<any[]>(
        `${API.PLACEMENT}/doplace/${thisPost.school_id}/${thisPost.position_id}`,
        { request_id: thisPost.requested_post_id }
      );

      if (res) {
        console.log("====================================");
        console.log({ doPlacement: res });
        console.log("====================================");
        this.props.fetchDistrictPosts(
          this.props.auth.user_access.district_id,
          (status: boolean, message: string) => {
            if (!status) {
              if (message.length > 0 && this.state.loadingPost) {
                console.error("error loading school post: ", message);
                this.props.PushPopupAlert({
                  title: message,
                  theme: PopupAlertTheme.danger,
                });
              }
              this.setLoadingPost(false);
            }
          }
        );

        let msgAlert: string = "";
        if (res.data.length > 0) {
          if (res.data.length === this.state.approvedPosts) {
            msgAlert = "All confirmed posts has been placed on school!";
          } else {
            msgAlert = `${res.data.length} of ${this.state.approvedPosts} post(s) has been placed on school!`;
          }
        } else {
          msgAlert = "Post request has been confirmed but no placement done!";
        }

        this.setThisPost(null);
        this.setApprovedPosts(0);
        this.setModalState(null);
        this.props.PushPopupAlert({
          title: msgAlert,
          theme:
            res.data.length > 0
              ? PopupAlertTheme.success
              : PopupAlertTheme.info,
        });
      }
    } catch (error) {
      console.error({ doPlacement_reply: error });
      this.props.PushPopupAlert({
        title: errorToText(error),
        theme: PopupAlertTheme.danger,
      });
    }
  };

  reDoPlacement = async (thisPost: PostSchoolInterface) => {
    try {
      const res = await axios.post<any[]>(
        `${API.PLACEMENT}/doplace/${thisPost.school_id}/${thisPost.position_id}`,
        { request_id: thisPost.requested_post_id }
      );

      if (res) {
        console.log("====================================");
        console.log({ reDoPlacement: res });
        console.log("====================================");
        this.props.fetchDistrictPosts(
          this.props.auth.user_access.district_id,
          (status: boolean, message: string) => {
            if (!status) {
              if (message.length > 0 && this.state.loadingPost) {
                console.error("error loading school post: ", message);
                this.props.PushPopupAlert({
                  title: message,
                  theme: PopupAlertTheme.danger,
                });
              }
              this.setLoadingPost(false);
            }
          }
        );

        let msgAlert: string = "";
        if (res.data.length > 0) {
          if (res.data.length === this.state.approvedPosts) {
            msgAlert = "All confirmed posts has been placed on school!";
          } else {
            msgAlert = `${res.data.length} of ${
              thisPost.occupied_posts
                ? thisPost.occupied_posts -
                  (thisPost.received_posts ? thisPost.received_posts : 0)
                : 0
            } post(s) has been placed on school!`;
          }
        } else {
          msgAlert =
            "Placement failed, Please wait for the next application period!";
        }

        this.props.PushPopupAlert({
          title: msgAlert,
          theme:
            res.data.length > 0
              ? PopupAlertTheme.success
              : PopupAlertTheme.info,
        });
      }
    } catch (error) {
      console.error({ doPlacement_reply: error });
      this.props.PushPopupAlert({
        title: errorToText(error),
        theme: PopupAlertTheme.danger,
      });
    }
  };

  addSchoolRequest = async (thisPost: PostSchoolInterface) => {
    const dataForm: {
      school_id: string;
      school_name: string;
      request_id: string;
      district_id: string;
      sector_id: string;
      province_id: string;
      gender: "M" | "F";
      available_places: {
        position_id: string;
        requested_teacher: number;
      }[];
    }[] = [
      {
        school_id: thisPost.school._id,
        school_name: thisPost.school.school_name,
        request_id: thisPost.requested_post_id,
        district_id: thisPost.school.district_id._id,
        sector_id: thisPost.school.sector_id,
        province_id: thisPost.school.province_id,
        gender: thisPost.male_number > thisPost.female_number ? "F" : "M",
        available_places: [
          {
            position_id: thisPost.position_id,
            requested_teacher: this.state.approvedPosts,
          },
        ],
      },
    ];

    let data = { requested_teachers: dataForm };
    console.log("====================================");
    console.log({ requested_teachers: dataForm, data });
    console.log("====================================");

    try {
      const res = await axios.post(`${API.PLACEMENT}/requestteachers/`, data);

      if (res) {
        console.log("====================================");
        console.log({ addSchoolRequest: res });
        console.log("====================================");
        await this.doPlacement(thisPost);
      }
    } catch (error) {
      console.error({ addSchoolRequest_reply: error });
      this.props.PushPopupAlert({
        title: errorToText(error),
        theme: PopupAlertTheme.danger,
      });
    }
  };

  submitRequest = async (e: React.FormEvent<HTMLFormElement>) => {
    e.preventDefault();
    if (!this.state.thisPost) {
      return;
    }

    if (this.state.approvedPosts < 0) {
      return this.setFormError({
        element: "approved_posts",
        msg: "Invalid number of approved post",
      });
    }

    if (this.state.approvedPosts > this.state.thisPost.needed_posts) {
      return this.setFormError({
        element: "approved_posts",
        msg: "Approved post can't be greater than needed post",
      });
    }

    this.setLoadingRequest(true);
    try {
      if (this.state.modalState === "CONFIRM") {
        const res = await axios.patch<{ data: PostSchoolInterface }>(
          `${API.PLACEMENT}/requestpost/${this.state.thisPost.requested_post_id}`,
          {
            confirmed_posts: this.state.approvedPosts,
            confirmed_by: this.props.auth.user.user_id,
            received_posts: 0,
            approval_date: new Date(),
            school_id: this.state.thisPost.school_id,
            position_id: this.state.thisPost.position_id,
            district_id: this.state.thisPost.district_id,
            needed_posts: this.state.thisPost.needed_posts,
            male_number: this.state.thisPost.male_number,
            female_number: this.state.thisPost.female_number,
            description: this.state.thisPost.description,
            occupied_posts: this.state.thisPost.occupied_posts,
            requested_by: this.props.auth.user.user_id,
            requested_post_status: PostRequestStatusInterface.CONFIRMED,
          }
        );
        if (res) {
          // props.updateSchoolPost("SCHOOL", res.data.data);
          await this.addSchoolRequest(this.state.thisPost);
        }
      }
    } catch (error) {
      console.error({ post_relpy: error });
      this.props.PushPopupAlert({
        title: errorToText(error),
        theme: PopupAlertTheme.danger,
      });
    }
    this.setLoadingRequest(false);
  };

  render() {
    if (isEmptyOrSpaces(this.props.auth.user_access.district_id)) {
      return <Redirect to="/" />;
    }

    if (this.state.loading || this.state.loadingPost) {
      return (
        <div className="text-center mt-7 font-bold text-4xl text-gray-400 animate-pulse">
          Loading data...
        </div>
      );
    }

    return (
      <div>
        {/* header title */}
        <div className="flex flex-row items-center gap-4 my-2 overflow-x-auto">
          <GiTeacher className="text-blue-600 text-5xl md:block hidden" />
          <div className="w-full">
            <div className="flex justify-between w-full">
              <div className="flex flex-col gap-1 justify-between">
                <h1 className="md:text-4xl text-3xl font-semibold">
                  Schools requests
                </h1>
              </div>
            </div>
          </div>
        </div>
        {/* end header title */}

        {/* ----------------------------------------------------------------------------- */}

        {/* CONTENT BODY */}
        <div className="bg-white shadow-md p-2 rounded overflow-x-auto">
          <div className="my-2 flex sm:flex-row flex-col justify-between">
            <div className="flex flex-col md:flex-row">
              <select
                className="border-2 border-gray-700 bg-white p-2 rounded focus:outline-none"
                value={this.state.filterByPosition}
                onChange={(e) => this.setFilterByPosition(e.target.value)}
              >
                <option value="all">All Position</option>
                {this.props.systemPositions &&
                this.props.systemPositions.length > 0 ? (
                  this.props.systemPositions.map((position, index) => (
                    <option key={index + 1} value={position.position_id}>
                      {position.position_title}
                    </option>
                  ))
                ) : (
                  <option value="">No Position found</option>
                )}
              </select>
            </div>
          </div>
          <Table
            columns={[
              { columnTitle: "SCHOOL", alignText: "left", padding: "p-3" },
              { columnTitle: "POSITION", alignText: "left", padding: "p-3" },
              { columnTitle: "REQUEST", alignText: "left", padding: "p-3" },
              { columnTitle: "APPROVED", alignText: "left", padding: "p-3" },
              { columnTitle: "OCCUPIED", alignText: "left", padding: "p-3" },
              { columnTitle: "VACCANTS", alignText: "left", padding: "p-3" },
              { columnTitle: "STATUS", alignText: "left", padding: "p-3" },
              { columnTitle: "", alignText: "left", padding: "p-3" },
            ]}
            rows={this.tableData}
            pagination={true}
            search={true}
            thColor="gray"
          />
        </div>
        {/* END CONTENT BODY */}

        {/* ----------------------------------------------------------------------------- */}

        {/* RESPOND TO REQUEST Modal */}
        {this.state.modalState &&
          this.state.thisPost &&
          this.props.systemPositions && (
            <Modal
              backDrop={true}
              theme={Themes.default}
              close={(): void => {
                this.setThisPost(null);
                this.setModalState(null);
              }}
              displayClose={true}
              backDropClose={false}
              title={
                <div className="flex flex-row gap-4 items-center">
                  <div className="flex flex-col">
                    <span className="font-bold text-2xl text-gray-500">
                      Responding to requested post
                    </span>
                  </div>
                </div>
              }
              widthSizeClass={ModalSize.large}
              padding={{ title: true, body: false, footer: false }}
            >
              <div className="px-4 mx-auto mb-4 mt-3">
                <div className="flex flex-col gap-3">
                  {/* LEFT SIDE OF THE SYSTEM ================================== */}
                  <div className="rounded bg-gray-100 border p-2">
                    <table>
                      <tbody>
                        <tr>
                          <td className="text-lg text-gray-700">School:</td>
                          <td className="font-medium px-4 text-lg text-gray-700">
                            {this.state.thisPost.school.school_name}
                          </td>
                        </tr>
                        <tr>
                          <td className="text-lg text-gray-700">Position:</td>
                          <td className="font-medium px-4 text-lg text-gray-700">
                            {getPositionById(
                              this.props.systemPositions,
                              this.state.thisPost.position_id
                            )
                              ? getPositionById(
                                  this.props.systemPositions,
                                  this.state.thisPost.position_id
                                )!.position_title
                              : ""}
                          </td>
                        </tr>
                        <tr>
                          <td className="text-lg text-gray-700">
                            Needed posts (number):
                          </td>
                          <td className="font-medium px-4 text-lg text-gray-700">
                            {this.state.thisPost.needed_posts}
                          </td>
                        </tr>
                        <tr>
                          <td className="text-lg text-gray-700">
                            Occupation (number):
                          </td>
                          <td className="font-medium px-4 text-lg text-gray-700">
                            {this.state.thisPost.occupied_posts}
                          </td>
                        </tr>
                        <tr>
                          <td className="text-lg text-gray-700">
                            Explanation:
                          </td>
                          <td className="font-medium px-4 text-lg text-gray-700">
                            {this.state.thisPost.description}
                          </td>
                        </tr>
                        <tr>
                          <td className="text-lg text-gray-700">
                            Requested date:
                          </td>
                          <td className="font-medium px-4 text-lg text-gray-700">
                            {DATE(this.state.thisPost.request_date.toString())}
                          </td>
                        </tr>
                      </tbody>
                    </table>
                  </div>
                  {/* END LEFT SIDE OF THE SYSTEM ================================== */}

                  {/* --------------------------------------------------------------------- */}

                  <div className="bg-white">
                    <form onSubmit={this.submitRequest}>
                      <div className="text-base leading-6 space-y-4 text-gray-700 sm:text-lg sm:leading-7">
                        <div className="flex flex-col">
                          <label>Approved posts (number)</label>
                          <input
                            type="number"
                            className={`appearance-none rounded w-full py-1 px-4 border-2 bg-white focus:outline-none focus:bg-gray-100 ${
                              this.state.formError !== null &&
                              this.state.formError.element === "approved_posts"
                                ? "border-red-500"
                                : "border-gray-700"
                            }`}
                            value={this.state.approvedPosts}
                            onChange={(e) => {
                              this.setApprovedPosts(Number(e.target.value));
                            }}
                            min={0}
                            max={this.state.thisPost.needed_posts}
                            placeholder="1"
                            disabled={this.state.loadingRequest}
                          />
                          {this.state.formError !== null &&
                            this.state.formError.element ===
                              "approved_posts" && (
                              <span className="text-red-600 font-bold text-xs">
                                {this.state.formError.msg}
                              </span>
                            )}
                        </div>
                      </div>
                      <div className="pt-4 flex justify-between">
                        <button
                          className="btn bg-gray-400 text-white hover:bg-gray-600"
                          onClick={() => {
                            this.setThisPost(null);
                            this.setApprovedPosts(0);
                            this.setModalState(null);
                          }}
                        >
                          Exit
                        </button>
                        <button
                          className="btn bg-primary text-white hover:bg-blue-600"
                          disabled={this.state.loadingRequest}
                        >
                          {this.state.loadingRequest ? "Loading..." : "Respond"}
                        </button>
                      </div>
                    </form>
                  </div>
                </div>
              </div>
            </Modal>
          )}
        {/* End of RESPOND TO REQUEST Modal */}

        {/* ----------------------------------------------------------------------------- */}

        {this.state.viewPost &&
          this.state.modalState === null &&
          this.state.thisPost &&
          this.props.systemPositions && (
            <Modal
              backDrop={true}
              theme={Themes.default}
              close={(): void => {
                this.setThisPost(null);
                this.setViewPost(false);
              }}
              displayClose={true}
              backDropClose={false}
              title={
                <div className="flex flex-row gap-4 items-center">
                  {/* <IoBriefcase className="text-5xl text-gray-500" /> */}
                  <div className="flex flex-col">
                    <span className="font-bold text-2xl text-gray-500">
                      Viewing Post Request
                    </span>
                  </div>
                </div>
              }
              widthSizeClass={ModalSize.extraLarge}
              padding={{ title: true, body: false, footer: false }}
            >
              <section className="px-4 mx-auto mb-4 mt-3 flex items-start">
                <section className="w-full">
                  <div className="flex flex-row item-center justify-left gap-3 mb-2">
                    <span className="text-gray-500">Position: </span>
                    <span className="font-bold">
                      {getPositionById(
                        this.props.systemPositions,
                        this.state.thisPost.position_id
                      )
                        ? getPositionById(
                            this.props.systemPositions,
                            this.state.thisPost.position_id
                          )!.position_title
                        : "Unknown position"}
                    </span>
                  </div>
                  <div className="my-2">
                    <table className="w-full border" id="report_content">
                      <thead className="bg-blue-50 border-b border-gray-300 text-gray-600 font-bold text-sm">
                        <tr>
                          <td className="px-2 py-2">Requested posts</td>
                          <td className="px-2 py-2">Approved posts</td>
                          <td className="px-2 py-2">Placed posts</td>
                          <td className="px-2 py-2">Received posts</td>
                          <td className="px-2 py-2">Not received posts</td>
                          <td className="px-2 py-2">Status</td>
                          <td></td>
                        </tr>
                      </thead>
                      <tbody>
                        <tr>
                          <td className="px-2 py-2">
                            {this.state.thisPost.needed_posts}
                          </td>
                          <td className="px-2 py-2">
                            {this.state.thisPost.requested_post_status !==
                            PostRequestStatusInterface.INITIATED ? (
                              <div className="flex items-center gap-1">
                                {this.state.thisPost.confirmed_posts}
                              </div>
                            ) : (
                              "-"
                            )}
                          </td>
                          <td className="px-2 py-2">
                            {this.props.districtPlacements &&
                            this.state.thisPost.requested_post_status !==
                              PostRequestStatusInterface.INITIATED ? (
                              <span>
                                {
                                  getPlacementByRequest(
                                    this.props.districtPlacements,
                                    this.state.thisPost.requested_post_id
                                  ).length
                                }
                              </span>
                            ) : (
                              "-"
                            )}
                          </td>
                          <td className="px-2 py-2">
                            {this.state.thisPost.requested_post_status ===
                            PostRequestStatusInterface.RECEIVED
                              ? this.state.thisPost.received_posts
                              : "-"}
                          </td>
                          <td className="px-2 py-2">
                            {this.props.districtPlacements &&
                            this.state.thisPost.requested_post_status ===
                              PostRequestStatusInterface.RECEIVED ? (
                              <span>
                                {
                                  getPlacementByRequest(
                                    this.props.districtPlacements,
                                    this.state.thisPost.requested_post_id,
                                    PlacedCandidateStatus.REJECTED
                                  ).length
                                }
                              </span>
                            ) : (
                              "-"
                            )}
                          </td>

                          <td className="px-2 py-2">
                            <span
                              className={`px-2 py-1 font-semibold rounded ${
                                this.state.thisPost.requested_post_status ===
                                PostRequestStatusInterface.INITIATED
                                  ? "text-gray-700 bg-gray-100 dark:bg-gray-700 dark:text-gray-100"
                                  : this.state.thisPost
                                      .requested_post_status ===
                                    PostRequestStatusInterface.CONFIRMED
                                  ? "text-blue-700 bg-blue-100 dark:bg-blue-700 dark:text-blue-100"
                                  : "text-green-800 bg-green-100 dark:bg-green-800 dark:text-green-100"
                              }`}
                            >
                              {this.props.districtPlacements
                                ? showRequestStatus(
                                    this.state.thisPost.requested_post_status,
                                    getPlacementByRequest(
                                      this.props.districtPlacements,
                                      this.state.thisPost.requested_post_id,
                                      "all"
                                    ).length,
                                    getPlacementByRequest(
                                      this.props.districtPlacements,
                                      this.state.thisPost.requested_post_id,
                                      PlacedCandidateStatus.PLACED
                                    ).length,
                                    getPlacementByRequest(
                                      this.props.districtPlacements,
                                      this.state.thisPost.requested_post_id,
                                      PlacedCandidateStatus.REJECTED
                                    ).length
                                  )
                                : showRequestStatus(
                                    this.state.thisPost.requested_post_status,
                                    0,
                                    0,
                                    0
                                  )}
                            </span>
                          </td>
                          <td>
                            {this.state.thisPost.requested_post_status ===
                              PostRequestStatusInterface.INITIATED && (
                              <button
                                className="font-bold py-1 px-4 rounded bg-gray-500 text-white hover:bg-gray-700"
                                onClick={() => {
                                  this.setModalState("UPDATE");
                                }}
                                disabled={
                                  this.state.thisPost.requested_post_status !==
                                  PostRequestStatusInterface.INITIATED
                                }
                              >
                                Reply
                              </button>
                            )}
                            {this.props.districtPlacements &&
                              getPlacementByRequest(
                                this.props.districtPlacements,
                                this.state.thisPost.requested_post_id,
                                PlacedCandidateStatus.PENDING
                              ).length === 0 &&
                              getPlacementByRequest(
                                this.props.districtPlacements,
                                this.state.thisPost.requested_post_id,
                                PlacedCandidateStatus.REJECTED
                              ).length > 0 && (
                                <button
                                  className="font-bold ml-2 py-1 px-4 rounded bg-gray-500 text-white hover:bg-gray-700"
                                  onClick={() =>
                                    this.reDoPlacement(this.state.thisPost!)
                                  }
                                  disabled={this.state.loadingRequest}
                                >
                                  Do placement
                                </button>
                              )}
                          </td>
                        </tr>
                      </tbody>
                    </table>
                  </div>
                  {getPositionById(
                    this.props.systemPositions,
                    this.state.thisPost.position_id
                  ) && (
                    <ViewPosition
                      thisPosition={
                        getPositionById(
                          this.props.systemPositions,
                          this.state.thisPost.position_id
                        )!
                      }
                      isGuest={true}
                    />
                  )}
                </section>
              </section>
            </Modal>
          )}
      </div>
    );
  }
}

const mapStateToProps = ({
  auth,
  schools,
  system_settings,
}: StoreState): {
  auth: Auth;
  districtPosts: PostSchoolInterface[];
  districtPlacements: placedCandidateInterface[] | null;
  systemPositions: PositionAPI[] | null;
} => {
  return {
    auth: auth,
    districtPosts: schools.districtPosts,
    districtPlacements: schools.districtPlacements,
    systemPositions: system_settings.systemPositions,
  };
};

export const District = connect(mapStateToProps, {
  getPositions,
  fetchDistrictPosts,
  getDistrictPlacement,
  updateSchoolPost,
  PushPopupAlert,
})(_District);
